import React, { Component } from 'react';
import userImg from '@/assets/img/bg.jpg';
import styles from './index.less';
class CharBox extends Component {
  render() {
    return (
      <div className={styles.user_info_box}>
        <img src={userImg} alt="" />
        <h4></h4>
        <p></p>
        <div>
          <span></span>
          <span></span>
          <span></span>
        </div>
        <button></button>
      </div>
    );
  }
}
export default CharBox;
